@font-face {
  font-family: "Open Sans";
  font-size: normal;
  font-weight: normal;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-size: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
:root {
  --basic-background-color: #ffffff;
  --accent-background-color: #f1f1f1;
  --basic-color-dark: #444444;
  --main-color: #000000;
  --accent-color: #63d1bb;
}
.link-reset {
  text-decoration: none;
  color: inherit;
}
.list-reset {
  list-style-type: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  width: 260px;
  margin: 0 auto;
  padding: 0 30px;
}
@media (min-width: 768px) {
  .container {
    width: 668px;
    padding: 0 50px;
  }
}
@media (min-width: 1150px) {
  .container {
    width: 1050px;
    padding: 0 50px;
  }
}
@media (min-width: 768px) {
  .highlight {
    background-color: var(--accent-background-color);
  }
}
.page {
  height: 100%;
}
.page__body {
  margin: 0;
  min-width: 320px;
  height: 100%;
  display: flex;
  flex-direction: column;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 17px;
  line-height: 24px;
  color: var(--basic-color-dark);
  background-color: var(--basic-background-color);
}
.page-main {
  flex-grow: 1;
}
.page-main--catalog {
  padding: 0 0 57px;
}
@media (min-width: 768px) {
  .page-main--catalog {
    padding-bottom: 64px;
  }
}
@media (min-width: 1150px) {
  .page-main--catalog {
    padding-bottom: 103px;
  }
}
@media (min-width: 768px) {
  .page-main {
    margin-top: -86px;
  }
}
.page-header__container {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .page-header__container {
    width: 668px;
    padding: 0 50px;
  }
}
@media (min-width: 1150px) {
  .page-header__container {
    width: 1050px;
    padding: 0 50px;
  }
}
.page-header__text {
  display: none;
  width: 500px;
  margin: 0;
  margin-left: auto;
  font-size: 15px;
  text-align: right;
}
@media (min-width: 768px) {
  .page-header__text {
    position: relative;
    display: block;
    z-index: 1;
    transform: translateY(-41px);
  }
}
.navigation {
  position: relative;
  display: grid;
  align-items: center;
  justify-items: center;
  color: var(--main-color);
  font-weight: 700;
  background-color: var(--basic-background-color);
}
.navigation--nojs {
  min-height: 395px;
}
@media (min-width: 768px) {
  .navigation--nojs {
    min-height: auto;
  }
}
@media (min-width: 768px) {
  .navigation {
    grid-template-columns: 317px 351px;
    justify-items: start;
    z-index: 1;
    background-color: transparent;
  }
}
@media (min-width: 1150px) {
  .navigation {
    grid-template-columns: 450px 150px 450px;
  }
}
.navigation__logo {
  padding: 22px 0;
  align-self: start;
}
@media (min-width: 1150px) {
  .navigation__logo {
    grid-column-start: 2;
    justify-self: center;
    padding: 9px 0;
  }
}
.navigation__toggle {
  position: absolute;
  top: 32px;
  right: 30px;
  z-index: 2;
  display: block;
  width: 21px;
  height: 20px;
  padding: 10px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.navigation__toggle:hover {
  opacity: 0.6;
}
.navigation__toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .navigation__toggle {
    display: none;
  }
}
.navigation--closed .navigation__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 2px;
  background-color: #231f20;
  box-shadow: 0 6px 0 0 #231f20, 0 12px 0 0 #231f20;
}
.navigation__list {
  margin: 0;
  padding: 0;
  background-color: var(--basic-background-color);
}
@media (min-width: 768px) {
  .navigation__list {
    background-color: transparent;
  }
}
.navigation--nojs .navigation__toggle {
  display: none;
}
.navigation--opened .navigation__toggle::before,
.navigation--opened .navigation__toggle::after {
  content: "";
  position: absolute;
  top: 6px;
  left: -4px;
  width: 29px;
  height: 2px;
  background-color: var(--main-color);
}
.navigation--opened .navigation__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.navigation--opened .navigation__toggle::after {
  transform: rotate(-45deg);
}
@media (max-width: 767px) {
  .navigation--closed .site-navigation {
    display: none;
  }
  .navigation--closed .user-navigation {
    display: none;
  }
  .navigation--opened .site-navigation {
    position: absolute;
    top: 79px;
    left: 0;
    z-index: 2;
  }
  .navigation--opened .user-navigation {
    position: absolute;
    top: 235px;
    left: 0;
    z-index: 2;
  }
  .navigation--nojs .site-navigation {
    display: block;
    z-index: 2;
  }
  .navigation--nojs .user-navigation {
    display: block;
    z-index: 2;
  }
}
.logo {
  cursor: pointer;
}
.logo:hover {
  opacity: 0.6;
}
.logo:active {
  opacity: 0.3;
}
.logo__image {
  display: block;
  width: 86px;
  height: 35px;
}
@media (min-width: 768px) {
  .logo__image {
    width: 113px;
    height: 46px;
  }
}
@media (min-width: 1150px) {
  .logo__image {
    width: 149px;
    height: 72px;
  }
}
.site-navigation {
  list-style-type: none;
  width: 100%;
}
@media (min-width: 768px) {
  .site-navigation {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-row: 2/3;
    column-gap: 16px;
    align-items: center;
    justify-items: start;
    font-size: 15px;
  }
}
@media (min-width: 1150px) {
  .site-navigation {
    grid-row: 1/3;
    column-gap: 0;
    max-width: 440px;
    font-size: 17px;
  }
}
.site-navigation__item {
  border-top: 1px solid #ececec;
}
@media (min-width: 768px) {
  .site-navigation__item {
    padding: 8px 0;
    border-top: none;
  }
}
@media (min-width: 1150px) {
  .site-navigation__item {
    padding: 16px 0;
  }
}
@media (min-width: 1150px) {
  .site-navigation__item:first-child {
    border-right: 1px solid #ececec;
  }
}
.site-navigation__item--further {
  display: none;
}
@media (min-width: 1150px) {
  .site-navigation__item--further {
    display: block;
    font-size: 15px;
    padding: 0;
  }
}
.site-navigation__link {
  text-decoration: none;
  color: inherit;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.site-navigation__link:hover {
  color: rgba(0, 0, 0, 0.6);
}
.site-navigation__link:active {
  color: rgba(0, 0, 0, 0.3);
}
.site-navigation__link-container {
  width: 320px;
  padding: 28px 20px 26px 76px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .site-navigation__link-container {
    width: auto;
    padding: 10px 0;
  }
}
@media (min-width: 1150px) {
  .site-navigation__link-container {
    padding: 17px 0;
    padding-right: 35px;
  }
}
.user-navigation {
  list-style-type: none;
  width: 100%;
}
@media (min-width: 768px) {
  .user-navigation {
    display: grid;
    grid-template-columns: repeat(2, auto);
    justify-self: end;
    align-items: center;
    width: auto;
    height: 100%;
  }
}
.user-navigation__item {
  border-top: 1px solid #ececec;
}
@media (min-width: 768px) {
  .user-navigation__item {
    border-left: 1px solid #ececec;
    border-top: none;
    height: 100%;
    display: flex;
  }
}
.user-navigation__link {
  text-decoration: none;
  color: inherit;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.user-navigation__link-container {
  width: 320px;
  padding: 28px 20px 26px 30px;
  display: flex;
  align-items: center;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .user-navigation__link-container {
    width: auto;
    padding: 10px 0;
  }
}
.user-navigation__icon {
  margin-right: 25px;
  fill: rgba(0, 0, 0, 0.2);
}
.user-navigation__icon--search {
  margin-left: 2px;
}
@media (min-width: 768px) {
  .user-navigation__icon {
    padding: 10px;
    margin: 0 22px;
  }
}
@media (min-width: 768px) {
  .user-navigation__name--search {
    display: none;
  }
}
.user-navigation__link:hover .user-navigation__icon {
  fill: var(--accent-color);
}
.user-navigation__link:active .user-navigation__icon {
  fill: rgba(99, 209, 187, 0.3);
}
.user-navigation__link:hover .user-navigation__name {
  color: rgba(0, 0, 0, 0.6);
}
.user-navigation__link:active .user-navigation__name {
  color: rgba(0, 0, 0, 0.3);
}
.intro {
  background: #f9f9f9 url("../img/index/hero-bg.jpg") no-repeat center top -17px;
  position: relative;
}
@media (min-width: 768px) {
  .intro {
    background-image: url("../img/index/hero-bg-tablet.jpg");
    background-position: center top;
  }
  .intro::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 24px;
    bottom: -13px;
    left: 0;
    background-image: url("../img/zigzag-line-white-tablet.svg");
    background-repeat: repeat-x;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .intro {
    background-image: url("../img/index/hero-bg-tablet@2x.jpg");
    background-size: 768px auto;
  }
}
@media (min-width: 1150px) {
  .intro {
    background-image: url("../img/index/hero-bg-desktop.jpg");
  }
  .intro::after {
    height: 34px;
    bottom: -18px;
    left: 0;
    background-image: url("../img/zigzag-line-white-desktop.svg");
  }
}
@media (min-width: 1150px) and (min-resolution: 2dppx) {
  .intro {
    background-image: url("../img/index/hero-bg-desktop@2x.jpg");
    background-size: 1150px auto;
  }
}
.intro__hero {
  padding: 53px 0 100px;
}
@media (min-width: 768px) {
  .intro__hero {
    padding: 135px 0 351px;
    border-top: 1px solid #ececec;
  }
}
@media (min-width: 1150px) {
  .intro__hero {
    padding: 139px 0 335px;
  }
}
.intro__title {
  margin: 20px auto;
}
@media (min-width: 768px) {
  .intro__title {
    width: 530px;
  }
}
@media (min-width: 1150px) {
  .intro__title {
    width: 700px;
  }
}
.intro__catalog {
  margin: 0;
  padding: 0;
}
.section-header {
