}
.heading {
  font-weight: 700;
  font-size: 27px;
  line-height: 30px;
  text-align: center;
  color: var(--main-color);
}
@media (min-width: 768px) {
  .heading {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .heading {
    font-size: 53px;
    line-height: 57px;
  }
}
.catalog-list {
  list-style-type: none;
}
@media (min-width: 768px) {
  .catalog-list {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
.catalog-list__link {
  margin: 0 -30px;
}
@media (min-width: 768px) {
  .catalog-list__link {
    margin: 0;
  }
}
.catalog-link {
  text-decoration: none;
  color: inherit;
  position: relative;
  display: block;
  box-sizing: border-box;
  padding: 24px 30px 31px;
  color: #ffffff;
  font-weight: 700;
  font-size: 19px;
}
@media (min-width: 768px) {
