}
.catalog-link--interior-items:active {
  background-color: #59d5bc;
}
.catalog-link--interior-items::after {
  position: absolute;
  content: "";
  width: 40px;
  height: 62px;
  top: 50%;
  right: 36px;
  transform: translateY(-50%);
  background-image: url("../img/index/interior-items-pic.svg");
  background-repeat: no-repeat;
}
.catalog-link--toys {
  background-color: #56c3b2;
}
.catalog-link--toys:hover {
  background-color: #46c1ae;
}
.catalog-link--toys:active {
  background-color: #46c1ae;
}
.catalog-link--toys::after {
  position: absolute;
  content: "";
  width: 60px;
  height: 60px;
  top: 50%;
  right: 27px;
  transform: translateY(-50%);
  background-image: url("../img/index/toys-pic.svg");
  background-repeat: no-repeat;
}
.catalog-link--interior-items:hover::after,
.catalog-link--toys:hover::after {
  opacity: 0.7;
}
.catalog-link__caption {
  margin: 0;
  width: 130px;
}
.top-product {
  position: relative;
  padding: 32px 0 42px;
}
.top-product::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 6px;
  bottom: 0;
  left: 0;
  background-image: url("../img/zigzag-line.svg");
  background-repeat: repeat-x;
}
@media (min-width: 768px) {
  .top-product::after {
    height: 17px;
    background-size: 100% 17px;
