  background-color: #59d5bc;
}
.advantages {
  padding: 31px 0 29px;
  display: grid;
}
@media (min-width: 768px) {
  .advantages {
    padding: 56px 0 83px;
  }
}
@media (min-width: 1150px) {
  .advantages {
    padding: 69px 0 87px;
  }
}
.advantages__title {
  margin: 0 0 28px;
}
@media (min-width: 768px) {
  .advantages__title {
